body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

.tooltip {
    border-radius: 5px;
    position: absolute;
    background-color: white;
    color: black;
    padding: 3px;
    position: relative;
    transform: translateY(-50px);
    opacity: 0;
    transition-duration: 0.2s;
    transition-property: opacity, transform;
    max-width: 1000px; /* Adjust as needed */
    white-space: pre-line; /* Ensure line breaks are respected */
}

.tooltip-header {
    background-color: rgb(194, 225, 246);
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 0px;
    padding: 5px;
}

.tooltip-data {
    background-color: rgb(228, 241, 251);
    font-size: 16px;
    margin-bottom: -10px;
    padding: 2px;
}

.tooltip-logo {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 50px; /* Adjust as needed */
    height: 50px; /* Adjust as needed */
}

.tooltip-button {
    display: block; /* Ensure the button is displayed as a block element */
    margin: auto; 
    margin-bottom: -15px; /* Add some margin to separate it from other elements */
    margin-top: -15px; /* Add some margin to separate it from other elements */
    padding: 5px 15px;
    background-color:rgb(194, 225, 246);
    color: black;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    pointer-events: auto;
}

.tooltip-video {
    width: 600px;
    height: 400px;
    border-radius: 5px; 
    margin-top: 10px;
}

.hide {
    opacity: 0;
    transform: translateY(-10px);
    transition-duration: 2s;
    pointer-events: none;
}

.show {
    opacity: 1;
    transform: translateY(-35px) translateX(-3px);
    pointer-events: auto;
}

#startRobotButton:hover {
    background-color: #45a049;
}

.tooltip:hover {
    opacity: 1;
    transform: translateY(-35px) translateX(-3px);
    pointer-events: auto;
}