body {
  margin: 0;
  font-family: Montserrat, sans-serif;
}

.tooltip {
  color: #000;
  opacity: 0;
  white-space: pre-line;
  background-color: #fff;
  border-radius: 5px;
  max-width: 1000px;
  padding: 3px;
  transition-property: opacity, transform;
  transition-duration: .2s;
  position: relative;
  transform: translateY(-50px);
}

.tooltip-header {
  background-color: #c2e1f6;
  margin-bottom: 0;
  padding: 5px;
  font-size: 20px;
  font-weight: bold;
}

.tooltip-data {
  background-color: #e4f1fb;
  margin-bottom: -10px;
  padding: 2px;
  font-size: 16px;
}

.tooltip-logo {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 5px;
  right: 5px;
}

.tooltip-button {
  color: #000;
  cursor: pointer;
  pointer-events: auto;
  background-color: #c2e1f6;
  border: none;
  border-radius: 5px;
  margin: -15px auto;
  padding: 5px 15px;
  font-size: 16px;
  display: block;
}

.tooltip-video {
  border-radius: 5px;
  width: 600px;
  height: 400px;
  margin-top: 10px;
}

.hide {
  opacity: 0;
  pointer-events: none;
  transition-duration: 2s;
  transform: translateY(-10px);
}

.show {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(-35px)translateX(-3px);
}

#startRobotButton:hover {
  background-color: #45a049;
}

.tooltip:hover {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(-35px)translateX(-3px);
}
